import "./index.scss";
import {axios} from "&/axios";
import { connect } from "react-redux";
import { getSpecialData } from "../../actions";
import { history } from "&/history";

const citys = [
    {txt:"武汉",name:"wuhan"},
    {txt:"上海",name:"shanghai"},
    {txt:"长沙",name:"changsha"},
    {txt:"重庆",name:"chongqing"},
    {txt:"南京",name:"nanjing"},
    {txt:"成都",name:"chengdu"},
    {txt:"广州",name:"guangzhou"},
    {txt:"北京",name:"beijing"},
]


@connect(
    state=>{
        return{
            data:state.toJS("data"),
        }
    }
)
export default class Special extends Component{
    constructor(){
        super();
        this.state = {
            index:0,
            city:"武汉",
            name:"wuhan"
        }
    }
    changeCity=(val,ind,pinyin)=>{
        this.setState({
            index:ind,
            city:val,
            name:pinyin
        })
        this.props.dispatch(getSpecialData({
            url:"/airbnb/house",
            params:{
                limit:4,
                city:val
            }
        },val))
    }
    showMore=()=>{
        sessionStorage.city=this.state.city;
        history.push("/houses");
    }
    componentDidMount(){
        if(!this.props.data.data.wuhan.length){
            this.props.dispatch(getSpecialData({
                url:"/airbnb/house",
                params:{
                    limit:4,
                    city:"武汉"
                }
            },"武汉"))
        };
    }
    showData=()=>{
        axios.get("/airbnb/no").then(res=>{
            console.log(res);
        })
    }
    gotoDetail=id=>{
        sessionStorage.houseid = id;
        history.push("/housedetail");
    }
    render(){
        const {
            index,
            name,
            city
        } = this.state;
        const {
            data,
            dispatch
        } = this.props;
        return (
            <div>
                <div className="title">
                    <h2>秋季特惠房源</h2>
                    <p>低至8折，可叠加使用礼券</p>
                    {/* <button onClick={this.showData}>显示全部</button> */}
                </div>
                
                <div className="tabto">
                    {
                        citys.map((city,i)=>{
                            return <button
                                className={i==index?"on":""}
                                key={i} 
                                onClick={()=>this.changeCity(city.txt,i,city.name)}>
                                {city.txt}
                            </button>
                        })
                    }
                </div>
                <div className="house">
                    {
                        data.data[name].map((d,i)=>{
                            return (
                                <div onClick={()=>this.gotoDetail(d.id)} className="msgbox" key={i}>
                                    <img src={d.picture_url} alt=""/>
                                    <h2>{d.name}</h2>
                                    <p>
                                        <span>{d.price}</span>
                                        {d.location_context&&<b>{d.location_context.display_name}</b>}
                                    </p>
                                </div>
                            )
                        })
                    }
                    <button onClick={this.showMore}>显示更多{city}的房源</button>
                </div>
            </div>
        )
    }
}