import "./index.scss";
import {axios} from "&/axios";
import {history} from "&/history";

export default class Story extends Component{
    constructor(){
        super();
        this.state = {
            storys:[]
        }
    }
    componentDidMount(){
        if(!this.state.storys.length){
            axios.post("/airbnb/story",{
                    limit:4,
                    skip:4,
                    type:"房源"
            }).then(res=>{
                this.setState({
                    storys:res.data.result
                })
            })
        }
    }
    showMore=()=>{
        history.push("/index/gushi");
    }
    gotoStory=id=>{
        sessionStorage.storyid = id;
        history.push("/storydetail");
    }
    render(){
        const {
            storys
        } = this.state;
        // console.log(this.state);
        return (
            <div>
                <div className="title">
                    <h2>精选旅行故事</h2>
                </div>
                <div className="storybox">
                    {
                        storys.map((story,i)=>{
                            return (
                                <div  onClick={()=>this.gotoStory(story._id)} key={i} className="onestory">
                                    <img src={story.cover_image_url} alt=""/>
                                    <p className="story_title">
                                        <span>{story.localized_article_location}</span>
                                        {story.title}
                                    </p>
                                    <p className="story_info">
                                        <span>
                                            <img src={story.author_object.picture_url} alt=""/>
                                            {story.author_object.first_name}
                                        </span>
                                        <span className="like_count">
                                            <i className="iconfont icon-dianzan"></i>
                                            <b>{story.like_count}</b>
                                        </span>
                                    </p>
                                </div>
                            )
                        })
                    }
                    <button onClick={this.showMore}>查看更多故事</button>
                </div>
            </div>
        )
    }
}