import {axios} from "&/axios";

export const testing = num =>(
    {
        type:"testing", 
        num
    }
)

export async function getSpecialData({url,params},city){
    const res = await axios.post(url,params);
    return {
        type:city,
        sd:res.data.result
    }
}

export async function getStoryData({url,params},story){
    const res = await axios.post(url,params);
    return {
        type:story,
        sd:res.data.result
    }
}

export const changeStatus = num => (
    {
        type:num
    }
)

export const copyCode = num => (
    {
        type:"code",
        code:num
    }
)