import "./index.scss";
import { SearchBar , Carousel } from 'antd-mobile';
import { history } from "&/history";
import Special from "~/Special";
import Hotplace from "~/Hotplace"
import Story from "~/Story";

const banner = [
    { title: "来这里食补贴秋膘", tag: "国内美食目的地", url: "https://z1.muscache.cn/pictures/hongbao/marquee/original/c06deecd-ab91-448e-9496-675b85ba500a.jpg" },
    { title: "领取精选房源专属券", tag: "抢手热门房源", url: "https://z1.muscache.cn/pictures/hongbao/marquee/original/7a4fdcad-2707-490f-9d9e-1ce246d752f8.jpg" },
    { title: "汇率「破 7」超值游", tag: "境外目的地", url: "https://z1.muscache.cn/pictures/hongbao/marquee/original/a47e57c3-a536-4d4b-bf96-da2da4aa5680.jpg" },
    { title: "享受秋日多彩美景", tag: "国内金秋目的地", url: "https://z1.muscache.cn/pictures/hongbao/marquee/original/85d151e6-5927-4d58-865d-44989f4d02bc.jpg" },
    { title: "非遗体验，欢迎新生代的你", tag: "溯.承", url: "https://z1.muscache.cn/pictures/hongbao/marquee/original/89caf306-c865-4fa8-812c-d1cd66d286f0.jpg" }
]

export default class Tansuo extends Component {
    constructor() {
        super();
        this.state = {
            logo: require("@/assets/airbnb1.png"),
            toggle: false
        }
    }
    scroll=()=>{
        window.addEventListener('scroll',()=>{  
            var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            if(scrollTop > 150){
                this.setState({
                    toggle: true
                })
            }else{
                this.setState({
                    toggle: false
                })
            }

        });
    }
    goSearch = () => {
        history.push("/search");
    }
    componentDidMount() {
        scrollTo(0, 0);
        this.scroll();
    }
    render() {
        const {
            toggle
        } = this.state;
        return (
            <div>
                <div className="header">
                    <div className="bannerbox">
                        <div className="top">
                            <div className="wrapper">
                                <img style={{ width: "0.6rem", height: "0.6rem" }} src={this.state.logo} alt="" />
                                <i style={{ fontSize: 32, color: "#fff" }} className="iconfont icon-xiugaigerentouxiang-" onClick={()=>{history.push("/index/wode")}}></i>
                            </div>
                        </div>
                        <div className="swipper">
                        <Carousel
                            autoplay={true}
                            infinite
                            autoplayInterval={2000}
                            afterChangtruncateSync
                            dotStyle={{
                                backgroundColor:"rgba(255, 255, 255, 0.5)",
                                height: 4,
                                width: 4,
                                margin:"0 2px"
                            }}
                            dotActiveStyle={{
                                backgroundColor:"rgba(255, 255, 255, 1)",
                                height: 4,
                                width: 16,
                                borderRadius: 2,
                                animation:"change 2s linear"
                            }}
                        >
                        {
                            banner.map((b, i) => {
                                return (
                                    <div key={i}>
                                        <img
                                            src={b.url}
                                            alt=""
                                            style={{ width: '100%', verticalAlign: 'top', height:'5.5rem'}}
                                            onLoad={() => {
                                                // 激活图片尺寸修改 height
                                                window.dispatchEvent(new Event('resize'));

                                            }}
                                        />
                                        <div className="txt">
                                            <p>{b.tag}</p>
                                            <h2>{b.title}</h2>
                                        </div>
                                    </div>
                                )
                            })
                        }
                        </Carousel>
                        </div>
                        <div className="bottom" style={{
                            position:toggle?"fixed":"absolute",
                            bottom:toggle?"":" -0.4rem",
                            top:toggle?"0":"",
                            height:"1rem",
                        }}>
                            <div className="wrapper" style={{
                                backgroundColor:toggle?"#fff":"",
                            }}>
                                <SearchBar onFocus={this.goSearch}
                                    style={{ borderRadius: "5px" }} placeholder="搜「北京」试试" maxLength={8} />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="guoqing">
                    <div className="wrapper">
                        <img src="https://z1.muscache.cn/pictures/hongbao/toolbox/original/262eda7e-f2b4-4fb7-923b-85b01cb7e50c.png" alt="" />
                    </div>
                </div>
                <div className="sections special">
                    <div className="wrapper">
                        <Special></Special>
                    </div>
                </div>
                <div className="sections hotplace">
                    <div className="wrapper">
                        <Hotplace></Hotplace>
                    </div>
                </div>
                <div className="sections hoshyo">
                    <div className="wrapper">
                        <h2>爱彼迎旅行保障</h2>
                        <div className="previewbox">
                            <ul>
                                <li>
                                    <img src="https://z1.muscache.cn/airbnb/static/5308a6c455e5061137f8ae9e9a9ac4fc.png" />
                                    <div className="txt">
                                        <p>
                                            <span className="bold">安心房源保障</span>
                                            人工审核所有中国房源页面信息，入住更放心
                                        </p>
                                    </div>
                                </li>
                                <li>
                                    <img src="https://z1.muscache.cn/airbnb/static/f142a9a066b66fcfea524d7c2f5dcf63.png" />
                                    <div className="txt">
                                        <p>
                                            <span className="bold">专业中文客服</span>
                                            (86) 400-120-8508、(86) 010-8783-3463，24 小时服务
                                        </p>
                                    </div>
                                </li>
                                <li>
                                    <img src="https://z1.muscache.cn/airbnb/static/da0afd2d0100f3c056e5c6778c1bed22.png" />
                                    <div className="txt">
                                        <p>
                                            <span className="bold">旅行安全保障</span>
                                            多重风险控制设计，全方位保障您的行程安全
                                        </p>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div className="sections story">
                    <div className="wrapper">
                        <Story></Story>
                    </div>
                </div>
            </div>
        )
    }
}