import "./index.scss";
import {axios} from "&/axios";
import { connect } from "react-redux";
import { getSpecialData } from "../../actions";
import {history} from "&/history";

const citys = [
    {txt:"成都",name:"chengdu",count:"29000"},
    {txt:"上海",name:"shanghai",count:"39000"},
    {txt:"重庆",name:"chongqing",count:"21000"},
    {txt:"北京",name:"beijing",count:"35000"},
    {txt:"杭州",name:"hangzhou",count:"19000"},
    {txt:"大理",name:"dali",count:"11000"},
]


@connect(
    state=>{
        return{
            data:state.toJS("hotplace"),
        }
    }
)
export default class Hotplace extends Component{
    constructor(){
        super();
        this.state = {
            index:0,
            city:"成都",
            name:"chengdu"
        }
    }
    changeCity=(val,ind,pinyin)=>{
        this.setState({
            index:ind,
            city:val,
            name:pinyin
        })
        this.props.dispatch(getSpecialData({
            url:"/airbnb/house",
            params:{
                limit:4,
                skip:4,
                city:val
            }
        },val+"hot"))
    }
    showMore=()=>{
        sessionStorage.city=this.state.city;
        history.push("/houses");
    }
    componentDidMount(){
        if(!this.props.data.hotplace.chengdu.length){
            this.props.dispatch(getSpecialData({
                url:"/airbnb/house",
                params:{
                    limit:4,
                    skip:4,
                    city:"成都"
                }
            },"成都hot"))
        };
    }
    showData=()=>{
        axios.get("/airbnb/no").then(res=>{
            console.log(res);
        })
    }
    gotoDetail=id=>{
        sessionStorage.houseid = id;
        history.push("/housedetail");
    }
    render(){
        const {
            index,
            name,
            city
        } = this.state;
        const {
            data,
            dispatch
        } = this.props;
        // console.log(this.props);
        return (
            <div>
                <div className="title">
                    <h2>国内热门目的地</h2>
                    {/* <button onClick={this.showData}>显示全部</button> */}
                    
                </div>
                <div className="tabto">
                    {
                        citys.map((city,i)=>{
                            return <button
                                className={i==index?"on":""}
                                key={i} 
                                onClick={()=>this.changeCity(city.txt,i,city.name)}>
                                {city.txt}
                                <p>{city.count}+房源</p>
                            </button>
                        })
                    }
                </div>
                <div className="hots">
                    {
                        data.hotplace[name].map((d,i)=>{
                            return (
                                <div onClick={()=>this.gotoDetail(d.id)} className="hotbox" key={i}>
                                    <img src={d.picture_url} alt=""/>
                                    <h2>{d.name}</h2>
                                    <p>
                                        <span>{d.price}</span>
                                        {d.location_context&&<b>{d.location_context.display_name}</b>}
                                    </p>
                                </div>
                            )
                        })
                    }
                </div>
                <button className="hotbtn" onClick={this.showMore}>查看更多{city}房源</button>
            </div>
        )
    }
}